.app-wrapper {
  margin: 0 auto;

  display: grid;
  padding: 0 20px;
  /* width: 1200px; */
  grid-template-areas:
    'h h h'
    'n c c'
    'n c c';
  grid-template-rows: 60px 1fr;
  grid-template-columns: minmax(150px, auto) 2fr 10fr;
}
.app-wrapper-content {
  grid-area: c;
}

body {
  background: rgba(109, 109, 15, 0.301);
}
