@import url('https://fonts.googleapis.com/css2?family=ZCOOL+KuaiLe&display=swap');
.header {
  grid-area: h;
  display: table;
  height: 40px;
}
.logo {
  padding-top: 1rem;
  font-family: 'ZCOOL KuaiLe', cursive;
  color: rgb(102, 102, 14);
  font-size: 25px;
  display: table-cell;
}
/* .header img {
  width: 55px;
} */
.loginBlock {
  display: table-cell;
  float: right;
}
.logButton {
  background-color: black;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 18px;
  border-radius: 4px;
}
.login {
  text-decoration: none;
  font-weight: bold;
}
